@font-face {
  font-family: 'Aileron';
  src: url("fonts/aileron/aileron-bold-webfont.woff2") format("woff2"), url("fonts/aileron/aileron-bold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: 'Aileron';
  src: url("fonts/aileron/aileron-semibold-webfont.woff2") format("woff2"), url("fonts/aileron/aileron-semibold-webfont.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'Aileron';
  src: url("fonts/aileron/aileron-light-webfont.woff2") format("woff2"), url("fonts/aileron/aileron-light-webfont.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

h1, h2, h3, h4, h5, h6,
p,
ol,
ul,
li,
span {
  font-family: inherit;
  margin: 0;
  padding: 0; }

h1, .h1 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2em;
  margin-bottom: 1.5em;
  font-size: 2.25rem; }
  h1 span, .h1 span {
    display: block; }

.primary-heading-text {
  display: block;
  margin-bottom: 1.25em; }

.sub-heading {
  text-transform: none;
  letter-spacing: 0.55em;
  line-height: 1.5;
  font-size: 0.55em;
  font-weight: 500; }

.site-title {
  margin-bottom: 5rem; }

h2, .h2 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2em;
  margin-bottom: 1.5em; }

h3, .h3 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2em;
  margin-bottom: 1.5em; }

h4, .h4 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2em;
  margin-bottom: 1.5em; }

h5, .h5 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2em;
  margin-bottom: 1.5em; }

h6, .h6 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2em;
  margin-bottom: 1.5em; }

.paragraph, .paragraph-lg, .paragraph-sm {
  line-height: 2;
  letter-spacing: 0.2em;
  font-size: 1.125rem;
  margin-bottom: 1.5em; }

.paragraph-lg {
  font-size: 1.125rem; }

.paragraph-sm {
  font-size: 0.8rem; }

html, body {
  background-color: #111111;
  color: #FFFFFF;
  font-family: 'Aileron';
  font-weight: 300; }

body {
  padding: 3rem; }

::selection {
  background: transparent; }

::-moz-selection {
  background: transparent; }

*:last-child {
  margin-bottom: 0; }

.content-block {
  max-width: 50rem; }

#main-content {
  margin: 0 auto;
  margin-top: 10rem;
  margin-top: 20vh;
  max-width: 60%; }
